export default {
  middleware: "auth",
  auth: "guest",
  layout: "none",

  data() {
    return {
      email: "",
      password: "",
      emailError: "Please enter Correct Email Address",
      passError: "Please enter Correct Password",
      eError: false,
      pError: false,
    };
  },

  methods: {
    async onLogIn() {
      try {
        let data = {
          email: this.email,
          password: this.password,
        };
        let eSuccess = false;
        let pSuccess = false;
        if (!this.checkEmail(data.email)) {
          this.eError = true;
        } else {
          this.eError = false;
          eSuccess = true;
        }
        if (!this.checkPassword(data.password)) {
          this.pError = true;
        } else {
          this.pError = false;
          pSuccess = true;
        }
        if (eSuccess && pSuccess) {
          let response = await this.$auth.loginWith("local", {
            data: {
              email: this.email,
              password: this.password,
            },
          });
          await this.$auth.fetchUser()
          await this.$store.dispatch("setLoggedUser");

          this.$router.push("/");
        }
      } catch (error) {
          this.pError = true
          this.eError = true
        console.log('error here', error);
      }
    },
    checkEmail(data) {
      return data.length > 0;
    },
    checkPassword(data) {
      return data.length > 0;
    },
  },
};
